
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background: #171717;
}
#title{
  margin: 0 !important;
  padding: .2em 0em;    
  color: #CDDC39;
  font-family: cursive;
}
input{
    background: #fff !important;
    width: 50% !important;
    border: none !important;
    padding: .4em 1em !important;
    border-radius: 1em !important;
    margin: 5px auto !important;
    outline: none;
}
.d-block, .w-100{
  background: no-repeat center;
  background-size: contain;  
  -webkit-box-shadow: none;  
          box-shadow: none;
  border-radius: 0% !important;
  box-shadow: none;
    min-height: 600px;
}
img{
border-radius: 100%;
padding: 5px;
margin-top: 10px;
-webkit-box-shadow: 0 0 20px #d2e7f0;
        box-shadow: 0 0 20px #d2e7f0;
}
@media (max-width: 480px){
img{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    margin: auto;
}
input{
    width: 90% !important;
}
}


.search{
	display: none;
}
@media(max-width: 480px){
.search{
		display: inline;
		color: #fff;
}
}


.one{
	background: url(https://images.pexels.com/photos/210922/pexels-photo-210922.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260) center no-repeat;
  background-size: cover;
  width: 100%;
  height: 678px;
}
#song {
		color: #00bcd4;
    font-family: segoe UI;
    font-size: 20px;
    display: block;
    padding: 1em !important;
    text-transform: uppercase;
}
.badge {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
label {
    margin: auto !important;
    padding: 0em 0em 0em .5em !important;
    font-size: 16px !important;
    font-weight: 100 !important;
}
.description {
  position: absolute;
  color: #CDDC39;
  text-align: center;
  padding: 4em;
  margin-top: 7em;
  width: 100%;
  background: #30a8b769;
}
@media (max-width: 480px) {
.one{
	background: url(https://images.pexels.com/photos/65718/pexels-photo-65718.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260) center no-repeat;
  height: 590px;
	background-size: cover;
}
.description {
  margin-top: 5em;
}
}

.playcount{
	color:#8bc34a;
}
.down{
	color: #00BCD4;
}
.down, .playcount{
	cursor: pointer;
	padding: 0em .2em;
}
.carousel-control-next, .carousel-control-prev {
    position: absolute;
    top: -70% !important;
	width: 25% !important;
}
.list-group-item {
    background: #171717;
    border-color: #9e9e9e1c;
    font-weight: 100;
		color: #fff;		
    font-family: segoe UI;		
    padding: .5rem 1rem !important;
		text-align: left
}
.list-group{
		margin: 0% 25%;
}
@media (max-width: 1200px) {
.list-group{
		margin: 0% 20%;
}
.carousel-control-next, .carousel-control-prev{
		width: 20% !important;
}
}
@media (max-width: 480px) {
.list-group{
		margin: 0%;
}
.carousel-control-next, .carousel-control-prev{
		width: 15% !important;
}
}
